import React from "react";
const Input = (props) => {
    const handleChange = (e) => {
        props.setNftAmount(e.target.value)
    }
    return (
        <div>
            <p className="nftAmountInput" onChange={handleChange} value={props.nftAmount}>{props.nftAmount}</p>
        </div>
    )
}
export default Input