import React, { useEffect, useState } from "react";
import { useMetaMask } from "metamask-react";
import { abi } from './abi';
import { nftContractAddress } from "./config";
import Input from "./input"
import Owned from "./owned"
import "./app.css"
import Video from "./Wide-Website-Rona2.mp4"
import Logo from "./logo.png"
import detectEthereumProvider from '@metamask/detect-provider';

function App() {
	const { status, connect, account, chainId, ethereum } = useMetaMask();
	const [nftAmount, setNftAmount] = useState(1);
	const [totalOwned, setTotalOwned] = useState(0);
	const [onWhitelist, setOnWhitelist] = useState(false);
	const [publicSaleState, setPublicSaleState] = useState(false);
	const [update, setUpdate] = useState(Date.now())
	const ethers = require('ethers');
	let provider = ""
	let signer = ""
	let contract = ""
	let gasPrice = 0
	let contractSigner = ""
	const checkTotal = () => {
		if (status === "connected") {
			new Promise((resolve, reject) => {
				let value = ownedAmount(account)
				resolve(value)
			})
				.then(data => {
					setTotalOwned(data)
				})
		}
	}
	useEffect(() => {
		checkTotal()
	}, [status])
	useEffect(() => {
		clearTimeout()
		setTimeout(() => {
			checkTotal()
			setUpdate(Date.now())
		}, 1000)
	}, [update])
	if (window.ethereum === undefined){
		return(
			<div className="mainContainer">
				<video autoPlay muted loop id="myVideo">
					<source src={Video} type="video/mp4" />
				</video>
				<div className="navBar"><a href="/roadmap.html">ROADMAP</a> | <a href="https://discord.gg/mKGr36CX36">DISCORD</a></div>
				<div className="mintContainer">
					<img className="logo" src={Logo} />
					<div>MetaMask not available :(</div>
				</div>
			</div>)
	}
	provider = new ethers.providers.Web3Provider(window.ethereum)
	signer = provider.getSigner();
	let contractAddress = nftContractAddress;
	contract = new ethers.Contract(contractAddress, abi, provider);
	gasPrice = provider.getGasPrice() * 0.1
	contractSigner = contract.connect(signer);
	const ownedAmount = async (account) => {
		let balance = await contract.balanceOf(account)
		let result = balance.toString()
		return result
	}
	const increaseInput = () => {
		if (nftAmount < 10) {
			setNftAmount(nftAmount + 1)
		}
	}
	const decreaseInput = () => {
		if (nftAmount > 1) {
			setNftAmount(nftAmount - 1)
		}
	}
	let thewidth = window.innerWidth
	let overrides = {};
	const publicMint = () => {
		let value = 0.1 * nftAmount
		let gasPrice = provider.getGasPrice()
		overrides.gasPrice = gasPrice
		overrides.gasLimit = 500000
		overrides.value = ethers.utils.parseEther(String(value))
		contractSigner.publicMint(nftAmount, overrides).catch((error) => {
			console.log(error)
			if (error.code === "INSUFFICIENT_FUNDS") {
				let nftPanel = document.getElementById('nftPanel')
				nftPanel.innerHTML = `<p style="color:red">Insufficient Funds</p>`
			}
			else if (error.code === "NETWORK_ERROR") {
				let nftPanel = document.getElementById('nftPanel')
				nftPanel.innerHTML = `<p style="color:red">Network Error</p>`
			}

		})

	}
	const publicSaleActive = () => {
		contractSigner.publicM().then(result => {
			if (result === false){
				setPublicSaleState(false)
			}
			else if (result === true){
				setPublicSaleState(true)
			}
		})
	}
	publicSaleActive()
	const verifyUser = () => {
		if (account != null) {
			contractSigner.verifyUser(account).then(result => {
				if (result === false){
					setOnWhitelist(false)
				}
				else if (result === true){
					setOnWhitelist(true)
				}
			})
		}
	}
	verifyUser()
	if (window.innerWidth < 600) {
		return (
			<div className="mainContainer">
				<video autoPlay muted loop id="myVideo">
					<source src={Video} type="video/mp4" />
				</video>
				<div className="navBar"><a href="/socials">DISCORD</a></div>
				<div className="mintContainer">
					<img className="logoMobile" src={Logo} />
					<div><p>Screen Width Too Small, Probably Mobile</p></div>
				</div>
			</div>
		)
	}
	if (publicSaleState === false) return (
		<div className="mainContainer">
			<video autoPlay muted loop id="myVideo">
				<source src={Video} type="video/mp4" />
			</video>
			<div className="navBar"><a href="/roadmap.html">ROADMAP</a> | <a href="/socials">DISCORD</a></div>
			<div className="mintContainer">
				<img className="logo" src={Logo} />
				<p className="notOnWhitelist">MINTING IS CURRENTLY CLOSED</p>
			</div>
		</div>

	)
	if (status === "initializing") return (
		<div className="mainContainer">
			<video autoPlay muted loop id="myVideo">
				<source src={Video} type="video/mp4" />
			</video>
			<div className="navBar"><a href="/roadmap.html">ROADMAP</a> | <a href="/socials">DISCORD</a></div>
			<div className="mintContainer">
				<img className="logo" src={Logo} />
				<div>Synchronisation with MetaMask ongoing...</div>
			</div>
		</div>
	)

	if (status === "unavailable") return (
		<div className="mainContainer">
			<video autoPlay muted loop id="myVideo">
				<source src={Video} type="video/mp4" />
			</video>
			<div className="navBar"><a href="/roadmap.html">ROADMAP</a> | <a href="https://discord.gg/mKGr36CX36">DISCORD</a></div>
			<div className="mintContainer">
				<img className="logo" src={Logo} />
				<div>MetaMask not available :(</div>
			</div>
		</div>
	)
	if (status === "notConnected") return (
		<div className="mainContainer">
			<video autoPlay muted loop id="myVideo">
				<source src={Video} type="video/mp4" />
			</video>
			<div className="navBar"><a href="/roadmap.html">ROADMAP</a> | <a href="https://discord.gg/mKGr36CX36">DISCORD</a></div>
			<div className="mintContainer">
				<img className="logo" src={Logo} />
				<button onClick={connect} className="connectBtn">Connect to MetaMask</button>
			</div>
		</div>
	)

	if (status === "connecting") return (
		<div className="mainContainer">
			<video autoPlay muted loop id="myVideo">
				<source src={Video} type="video/mp4" />
			</video>
			<div className="navBar"><a href="/roadmap.html">ROADMAP</a> | <a href="https://discord.gg/mKGr36CX36">DISCORD</a></div>
			<div className="mintContainer">
				<img className="logo" src={Logo} />
				<div>Connecting...</div>
			</div>
		</div>
	)

	if (status === "connected" && onWhitelist === true) return (
		<div className="mainContainer">
			<video autoPlay muted loop id="myVideo">
				<source src={Video} type="video/mp4" />
			</video>
			<div className="navBar"><a href="/roadmap.html">ROADMAP</a> | <a href="https://discord.gg/mKGr36CX36">DISCORD</a></div>
			<div className="mintContainer">
				<img className="logo" src={Logo} />
				<Owned totalOwned={totalOwned} setTotalOwned={setTotalOwned} />
				<div className="nftAmountPanel"><a className="mintBtnLeft" onClick={decreaseInput}>&lt;</a><Input setNftAmount={setNftAmount} nftAmount={nftAmount} /><a className="mintBtnRight" onClick={increaseInput}>&gt;</a></div>
				<div id="nftPanel"></div>
				<button className="mintBtn" onClick={publicMint}>MINT NOW</button>
			</div>
		</div>
	)
	if (status === "connected" && onWhitelist === false) return (
		<div className="mainContainer">
			<video autoPlay muted loop id="myVideo">
				<source src={Video} type="video/mp4" />
			</video>
			<div className="navBar"><a href="/roadmap.html">ROADMAP</a> | <a href="https://discord.gg/mKGr36CX36">DISCORD</a></div>
			<div className="mintContainer">
				<img className="logo" src={Logo} />
				<div id="nftPanel"></div>
				<p className="notOnWhitelist">NOT ON WHITELIST <br></br>CONTACT US ON DISCORD</p>
			</div>
		</div>
	)
	else return (
		<div className="mainContainer">
			<video autoPlay muted loop id="myVideo">
				<source src={Video} type="video/mp4" />
			</video>
			<div className="navBar"><a href="/roadmap.html">ROADMAP</a> | <a href="https://discord.gg/mKGr36CX36">DISCORD</a></div>
			<div className="mintContainer">
				<img className="logo" src={Logo} />
				<div>MetaMask not available :(</div>
			</div>
		</div>
	)
}
export default App